@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";
$prefix: '.rise-blocks';
#{$prefix}-counter-wrapper{
    display: flex;
    margin: 0 -16px;
    flex-wrap: wrap;

    &#{$prefix}-items-per-row-1{
        > div{
            max-width: 100%;
            flex: 100%;
        }
    }
    
    &#{$prefix}-items-per-row-2{
        > div{
            max-width: 50%;
            flex: 50%;

            @include media(sm){ 
                max-width: 100%;
                flex: 100%;
            }
        }
    }

    &#{$prefix}-items-per-row-3{
        > div{
            max-width: 33.33333334%;
            flex: 33.333334%;

            @include media(md){ 
                max-width: 50%;
                flex: 50%;
            }

            @include media(xs){ 
                max-width: 100%;
                flex: 100%;
            }
        }
    }

    &#{$prefix}-items-per-row-4{
        > div{
            max-width: 25%;
            flex: 25%;

            // @include media(md){ 
            //     max-width: 33.333333334%;
            //     flex: 33.333333334%;
            // }

            @include media(xs){ 
                max-width: 50%;
                flex: 50%;
            }
        }
    }

    &#{$prefix}-items-per-row-5{
        > div{
            max-width: 20%;
            flex: 20%;

            @include media(md){ 
                max-width: 25%;
                flex: 25%;
            }

            @include media(xs){ 
                max-width: 33.333333334%;
                flex: 33.333333334%;
            }

            @include media(mb){ 
                max-width: 50%;
                flex: 50%;
            }
        }
    }

    &#{$prefix}-items-per-row-6{
        > div{
            max-width: 16.666666667%;
            flex: 16.666666667%;

            @include media(md){ 
                max-width: 20%;
                flex: 20%;
            }
           
            @include media(sm){ 
                max-width: 25%;
                flex: 25%;
            }
            
            @include media(xs){ 
                max-width: 33.333333334%;
                flex: 33.333333334%;
            }

            @include media(mb){ 
                max-width: 50%;
                flex: 50%;
            }
        }
    }

    > div{
        padding: 0 15px;
        margin: 1rem 0;

        #{$prefix}-counter{
            background-color: #fff;
        }
    }
}

/* Counter Style */
#{$prefix}-counter{
    padding: 1.5rem 1rem;
    text-align: center;
    word-break: break-all;

    &#{$prefix}-box-shadow{
        box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.1);
    }

    &#{$prefix}-box-border{
        border: 1px solid #ddd;
    }

    #{$prefix}-counter-number{
        font-size: 2rem;
        font-weight: 600;  
    }
    
    #{$prefix}-counter-line{
        width: 30px;
        height: 2px;
        background-color: #000;
        margin: 0.5rem auto;
    }

    #{$prefix}-counter-text{
        color: #000;
        text-transform: uppercase;
        font-weight: 600;
    }
}